import React, { useState } from "react";
import {
  Row,
  Col,
  Form,
  Button,
  Schema,
  Panel,
  SelectPicker,
  ButtonToolbar,
} from "rsuite";

import './styles.css';
import './form.less';
import { IS_INTERNAL } from '../../constants';

const { StringType, NumberType } = Schema.Types;

const PAYMENT_TYPE = [
  { label: 'Exact amount in fiat equivalent', value: 'fiat' },
  { label: 'Exact amount in crypto', value: 'crypto' },
]
const FIAT_CURRENCY = [
  { label: 'EUR', value: 'EUR' },
  { label: 'USD', value: 'USD' },
  { label: 'GBP', value: 'GBP' }
]
const CHAINS = [
  { label: 'BITCOIN', value: 'BITCOIN' },
  { label: 'LITECOIN', value: 'LITECOIN' },
  { label: 'POLYGON', value: 'POLYGON' }
]
const CRYPTO_CURRENCY = [
  { label: 'BTC', value: 'BTC' },
  { label: 'LTC', value: 'LTC' },
  { label: 'USDT', value: 'USDT' }
]

const model = Schema.Model({
  paymentAmount: NumberType(),
  address: StringType().isRequired('Address is required'),
  externalId: StringType().isRequired('External Id is required'),
  merchantId: StringType().isRequired('Merchant Id is required'),
  paymentType: StringType().isRequired('Payment Type is required'),
  currency: StringType().isRequired('Currency is required'),
  chain: StringType().isRequired('Chain is required'),
  email: StringType().isEmail('Please enter a valid email address.'),
});

const TextField = React.forwardRef((props, ref) => {
  const { name, label, accepter, ...rest } = props;
  return (
    <Form.Group controlId={`${name}-4`} ref={ref}>
      <Form.ControlLabel>{label} </Form.ControlLabel>
      <Form.Control name={name} accepter={accepter} {...rest} />
    </Form.Group>
  );
});

const Field = React.forwardRef((props, ref) => {
  const { name, label, accepter, error, ...rest } = props;
  return (
    <Form.Group controlId={`${name}-10`} ref={ref} className={error ? 'has-error' : ''}>
      <Form.ControlLabel>{label} </Form.ControlLabel>
      <Form.Control
        name={name}
        accepter={accepter}
        errorMessage={error}
        className="widget__form-control"
        {...rest}
      />
    </Form.Group>
  );
});

const getRandomExternalId = () => Math.floor(Math.random() * 9000000000) + 1000000000

const DEFAULT_FORM_VALUE = {
  paymentAmount: 200,
  address: 'address',
  externalId: 'some id',
  email: '',
  merchantId: 'some id',
  postbackUrl: '',
  paymentMethodId: '',
  chain: 'BITCOIN',
  currency: 'BTC',
  paymentCurrency: 'EUR',
  paymentType: PAYMENT_TYPE[0].value
}

export const WidgetForm = ({onSubmit}) => {
  const formRef = React.useRef();
  const [formError, setFormError] = React.useState({});
  const [selectedPaymentType, setSelectedPaymentType] = useState(PAYMENT_TYPE[0].value)

  const persistedFormValue = localStorage.getItem('formValue')
  const initialFormValue = persistedFormValue ? JSON.parse(persistedFormValue) : DEFAULT_FORM_VALUE
  const [formValue, setFormValue] = React.useState(initialFormValue);
  const [externalId, setExternalId] = useState(getRandomExternalId())

  React.useEffect(() => {
    onSubmit(formValue);
  }, [])

  const handleSubmit = () => {
    if (!formRef.current.check()) {
      console.error('Form Error', formError);
      return;
    }

    const {paymentAmount, ...rest} = formValue;
    const deepLinkOptions = { ...rest }

    if (selectedPaymentType === 'crypto') {
      deepLinkOptions.amount = Number(paymentAmount)
    } else {
      deepLinkOptions.paymentAmount = Number(paymentAmount)
    }
    const prevOptions = localStorage.getItem('formValue')
    const nextOptions = JSON.stringify(deepLinkOptions)
    if (prevOptions === nextOptions) {
      return
    }
    localStorage.setItem('formValue', nextOptions)
    onSubmit(deepLinkOptions)
    setExternalId(getRandomExternalId())
  };

  return (
    <Form
      ref={formRef}
      onChange={setFormValue}
      onCheck={setFormError}
      formValue={formValue}
      model={model}
    >
      <Panel className="widget__form-panel" shaded>
        <Row>
          <Col>
            <Panel header="Payment details">
              <Field
                name="paymentType"
                label="Payment type"
                accepter={SelectPicker}
                error={formError.status}
                data={PAYMENT_TYPE}
                onChange={(type) => setSelectedPaymentType(type)}
              />
              <TextField name="paymentAmount" label="Payment amount" />
              {IS_INTERNAL && (
                <>
                  <TextField name="currency" label="Crypto currency" />
                  <TextField name="chain" label="Blockchain" />
                  <TextField name="paymentCurrency" label="Fiat currency" />
                </>
              )}
              {!IS_INTERNAL && (
                <>
                  <Field
                    name="currency"
                    label="Crypto currency"
                    accepter={SelectPicker}
                    error={formError.status}
                    data={CRYPTO_CURRENCY}
                  />
                  <Field
                    name="chain"
                    label="Blockchain"
                    accepter={SelectPicker}
                    error={formError.status}
                    data={CHAINS}
                  />
                  {selectedPaymentType === 'fiat' && (
                    <Field
                      name="paymentCurrency"
                      label="Fiat currency"
                      accepter={SelectPicker}
                      error={formError.status}
                      data={FIAT_CURRENCY}
                    />
                  )}
                </>
              )}
            </Panel>
            <Panel header="Client details">
              <TextField name="address" label="Deposit Address" />
              <TextField name="email" label="End client’s valid email" />
            </Panel>
          </Col>
          <Col>
            <Panel header="Technical details">
              <TextField name="merchantId" label="Utorg’s SID" disabled value="hIiKzEM_7v" />
              <TextField name="externalId" label="Internal Order Id" disabled value={externalId} />
              {IS_INTERNAL && (
                <TextField name="postbackUrl" label="Postback url" />
              )}
            </Panel>
          </Col>
        </Row>
        <Panel style={{ overflow: 'visible' }}>
          <ButtonToolbar className="widget__button-toolbar">
            <Button className="widget_form-button" block appearance="primary" onClick={handleSubmit}>
              Submit
            </Button>
          </ButtonToolbar>
        </Panel>
      </Panel>
    </Form>
  );
};
