import React from 'react';
import { Col, FlexboxGrid } from 'rsuite';

import './App.css';
import { Widget, WidgetForm } from './components';

function App() {
  const [options, setOptions] = React.useState({})

  return (
    <FlexboxGrid justify="center" align="middle">
      <FlexboxGrid.Item as={Col} className="widget__col">
        <WidgetForm onSubmit={(data) => setOptions(data)} />
      </FlexboxGrid.Item>
      <FlexboxGrid.Item as={Col} className="widget__col">
        <Widget controlledOptions={options} />
      </FlexboxGrid.Item>
    </FlexboxGrid>
  );
}

export default App;
